import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ToddWritesCodeModule } from './todd-writes-code.module';
import { ToddWritesCodeDetailsComponent } from './components';

const routes: Routes = [
  {
    path: '',
    component: ToddWritesCodeDetailsComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes), ToddWritesCodeModule],
  exports: [RouterModule]
})
export class ToddWritesCodeRoutingModule { }



